import HomePage from "./homePage.react";
import ProductListPage from "./productListPage.react";
import NotFoundPage from "./notFoundPage.react";

const exact = true;

const baseRoutes = [
    exactRoute( "/", HomePage, exact ),
    exactRoute( "/productList", ProductListPage ),
    exactRoute( "/404", NotFoundPage ),
];

function exactRoute( path, component, exactCondition ) {
    const route = { path, component };

    if ( exactCondition ) {
        route.exact = true;
    }

    return route;
}

export { baseRoutes };
